import React from 'react';
import { Link } from 'react-router-dom';

function Navbar(props) {
  return (
    <div>
      <ul>
        <li>
          <Link to='/library' onClick={props.closeMenu}>
            Home
          </Link>
        </li>
        <li>
          <Link to='/library/about' onClick={props.closeMenu}>
            About
          </Link>
        </li>
      </ul>
    </div>
  );
}

export default Navbar;
